<template>
  <div>
    <b-breadcrumb :items="menus"/>
    <b-container fluid>
      <div class="addCon">
        <b-button variant="warning" @click="addKit">+ 邮箱配置</b-button>
        <b-modal ref="addMailModal" hide-footer no-close-on-backdrop title="邮箱 -- 添加">
          <b-form class="smForm" style="text-align:left" @submit.prevent="onSubmit($event)">
            <b-form-group id="add_sale_nameGroup" label="姓名:" label-size="sm" label-for="add_sale_name">
              <b-form-input autocomplete="off" type="text" id="add_sale_name" v-model.trim="addForm.sale_name" required>
              </b-form-input>
            </b-form-group>
            <b-form-group id="add_sale_emailNameGroup" label="邮箱名称:" label-size="sm" label-for="add_sale_emailName">
              <b-input-group>
                <b-form-input autocomplete="off" type="text" id="add_sale_emailName" v-model.trim="addForm.sale_emailName" required :state="addSaleEmailState">
                </b-form-input>
                <b-input-group-text slot="append" size="sm">
                  <span>@singleragenomics.com</span>
                </b-input-group-text>
              </b-input-group>
            </b-form-group>
            <b-form-group id="add_sale_phoneGroup" label="手机号:" label-size="sm" label-for="add_sale_phone">
              <b-form-input autocomplete="off" type="text" id="add_sale_phone" v-model.trim="addForm.sale_phone" :state="addSalePhoneState">
              </b-form-input>
            </b-form-group>
            <b-button type="submit" style="width:100%" variant="info">添加</b-button>
          </b-form>
        </b-modal>
      </div>
      <b-table bordered responsive hover :items="mailLists" :fields="fields" head-variant='light' show-empty :empty-text="tableEmptyText">
        <template slot="operate" slot-scope="row">
          <b-button size="sm" class="mr-2" variant="outline-theme" @click="edit(row)">
           编辑
          </b-button>
          <b-button size="sm" class="mr-2" variant="danger" @click="openDeleteModal(row)">
           删除
          </b-button>
          <b-modal :ref="'deleteMailModal'+row.index" title="邮箱配置 -- 删除" ok-variant="info" cancel-variant="link" cancel-title="取消" ok-title="确定" @ok="deleteMail(row)">
            <p class="my-4" style="text-align:left">是否确定要删除：<b>{{row.item.sale_name}}</b> ？</p>
          </b-modal>
          <b-modal :ref="'editMailModal'+row.index" hide-footer no-close-on-backdrop title="邮箱配置 -- 编辑">
            <b-form class="smForm" style="text-align:left" @submit.prevent="onSubmit($event,row)">
              <b-form-group :id="'edit_sale_nameGroup'+row.index" label="姓名:" label-size="sm" :label-for="'edit_sale_name'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_sale_name'+row.index" v-model.trim="currentForm.sale_name" required>
                </b-form-input>
              </b-form-group>
              <b-form-group :id="'edit_sale_emailGroup'+row.index" label="邮箱地址:" label-size="sm" :label-for="'edit_sale_email'+row.index">
                <b-form-input autocomplete="off" type="text" :id="'edit_sale_email'+row.index" v-model.trim="currentForm.sale_email" required :state="editSaleEmailState">
                </b-form-input>
              </b-form-group>
              <b-form-group :id="'edit_sale_phoneGroup'+row.index" label="手机号:" label-size="sm" :label-for="'edit_sale_phone'+row.index">
                <b-form-input autocomplete="off" size="sm" type="text" :id="'edit_sale_phone'+row.index" v-model.trim="currentForm.sale_phone" :state="editSalePhoneState">
                </b-form-input>
              </b-form-group>
              <b-button type="submit" style="width:100%" variant="info">提交</b-button>
            </b-form>
          </b-modal>
        </template>
      </b-table>
      <div class="paginationCon">
        <b-pagination size="md" :number-of-pages="10" :total-rows="total" v-model="currentPage" @input="changePage" :per-page="10"></b-pagination>
      </div>
    </b-container>
  </div>
</template>
<script>
  export default{
    mounted(){
      
      this.getTableData()
      
    },
    data () {
      return {
        menus: [{
          text: '邮箱配置管理',
          href: '#'
        }],
        addForm:{
          sale_name:'',
          sale_emailName:'',
          sale_phone:''
        },
        currentForm:{
          sale_name:'',
          sale_email:'',
          sale_phone:''
        },
        fields: {
          sale_name:{
            label:'姓名'
          },
          sale_email:{
            label:'邮箱地址'
          },
          sale_phone:{
            label:'手机号'
          },
          operate:{
            label:'操作',
            thStyle:{
              width:'150px'
            }
          }
        },
        tableEmptyText:'正在请求数据...',
        mailLists: [],
        currentPage:1,
        total:0
      }
    },
    computed: {
      addSaleEmailState(){
        // 注意这里的正则和编辑的不同
        if(this.addForm.sale_emailName&&this.addForm.sale_emailName.length!==0){
          let reg = /^[a-zA-Z.]+$/;
          if(!reg.test(this.addForm.sale_emailName)||this.addForm.sale_emailName.length>20){
            return false
          }else{
            return true
          }
        }else{
          return null
        }
      },
      addSalePhoneState () {
        if(this.addForm.sale_phone&&this.addForm.sale_phone.length!==0){
          let reg = /^[1][3,4,5,7,8][0-9]{9}$/;
          if(!reg.test(this.addForm.sale_phone)){
            return false
          }else{
            return true
          }
        }else{
          return null
        }        
      },
      editSaleEmailState(){
        // 注意这里的正则和添加的不同
        if(this.currentForm.sale_email&&this.currentForm.sale_email.length!==0){
          let reg = /^[a-zA-Z.]+@singleragenomics.com$/;
          if(!reg.test(this.currentForm.sale_email)||this.currentForm.sale_email.split("@")[0].length>20){
            return false
          }else{
            return true
          }
        }else{
          return null
        }
      },
      editSalePhoneState () {
        if(this.currentForm.sale_phone&&this.currentForm.sale_phone.length!==0){
          let reg = /^[1][3,4,5,7,8][0-9]{9}$/; 
          if(!reg.test(this.currentForm.sale_phone)){
            return false
          }else{
            return true
          }
        }else{
          return null
        }        
      },
    },
    methods:{
      getTableData(){
        this.$Loading.start()
        this.tableEmptyText = "正在请求数据..."
        this.$http.post("/api/sale/details",qs.stringify({page:this.currentPage})).then(response=>{
          // console.log(response.data)
          this.mailLists=response.data.data
          Number(response.data.total)===0?this.tableEmptyText = "[空]":null
          this.total = Number(response.data.total)
          this.$Loading.finish()
        })
      },
      addKit(){
        this.$refs.addMailModal.show()
      },
      edit(row){
        if(this.currentForm.salemailid!==row.item.salemailid){
          for(var k in row.item){
            this.currentForm[k]=row.item[k]
          }
        }
        this.$refs['editMailModal'+row.index].show()
      },
      openDeleteModal(row){
        this.$refs['deleteMailModal'+row.index].show()
      },
      deleteMail(row){
        this.$http.post("/api/sale/del",qs.stringify({salemailid:row.item.salemailid})).then(response=>{
          this.getTableData()
          this.$Loading.finish()
        })
      },
      onSubmit(evt,row){
        // this.$Loading.start()
        evt.preventDefault();
        // 编辑
        if(row){
          if(this.editSaleEmailState&&(this.editSalePhoneState===null||this.editSalePhoneState===true)){
            this.$http.post("/api/sale/edit",qs.stringify(this.currentForm)).then(response=>{
              this.getTableData()
              this.currentForm={
                sale_name:'',
                sale_email:'',
                sale_phone:''
              }
              this.$refs['editMailModal'+row.index].hide()
              this.$Loading.finish()
            })
          }
        }else{
          // 添加
          if(this.addSaleEmailState&&(this.addSalePhoneState===null||this.addSalePhoneState===true)){
            // console.log(this.addForm)
            this.addForm.sale_email = this.addForm.sale_emailName + "@singleragenomics.com"
            this.$http.post("/api/sale/add",qs.stringify(this.addForm)).then(response=>{
              // console.log(response)
              // if(String(response).indexOf('Error')){
              //   this.$Message.error('姓名或邮箱地址已存在！')
              // }
              this.getTableData()
              this.addForm={
                sale_name:'',
                sale_emailName:'',
                sale_phone:''
              }
              this.$refs.addMailModal.hide()
              this.$Loading.finish()
            }).catch(err=>{
              console.log(err)
            })
          }
        }
      },
      changePage(){
        this.getTableData()
      }
    }

  }
</script>
<style lang='less' scoped>
@import '../../../less/init.less';
.btn-sm{
  padding: .08rem .5rem !important;
}
.addCon{
  text-align: right;
  padding-right: 20px;
  margin-bottom:10px;
}
.paginationCon{
  overflow: hidden;
  .pagination{
    float: right;
  }
}

</style>


